import ModuleCard from "./ModuleCard";

const MODULES = [
  {
    id: "autoblogging",
    name: "Auto Blogging AI",
    description: "Genera y publica contenido automáticamente.",
    price: "Gratis",
  },
  {
    id: "shop-ai",
    name: "Shop AI",
    description: "Optimiza productos y precios con IA.",
    price: "19€/mes",
  },
  {
    id: "analytics",
    name: "Analytics Pro",
    description: "Decisiones basadas en datos inteligentes.",
    price: "29€/mes",
  },
  {
    id: "providers",
    name: "Gestión de Proveedores",
    description: "Automatiza sourcing y comunicación.",
    price: "Premium",
  },
];

export default function ModuleGrid() {
  return (
    <div className="mt-12 grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
      {MODULES.map((mod) => (
        <ModuleCard key={mod.id} module={mod} />
      ))}
    </div>
  );
}
